/* App.css */

/* General body styling */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f2f5;
  margin: 0;
  padding: 0;
}

/* Container for the app content */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Header styling */
h1 {
  font-size: 48px;
  color: #007bff;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 700;
}

/* Optional: Add icon styling if you're using icon fonts or SVGs */
h1::before {
  content: '\f0c0'; /* Example icon code for a user or dashboard icon */
  font-family: 'FontAwesome'; /* Example icon font */
  margin-right: 10px;
}

/* Add background color and padding for the main content */
.main-content {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
