/* DailyCommitmentForm.css */

/* General container styling */
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Heading styling */
.container h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

/* Form styling */
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Label styling */
label {
  font-weight: bold;
  color: #555;
}

/* Input styling */
input[type="date"],
input[type="text"],
input[type="number"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

/* Button styling */
button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Error message styling */
.error {
  color: #d9534f;
  font-size: 14px;
  margin-bottom: 15px;
  text-align: center;
}

/* Optional: Add icon styling if you're using icon fonts or SVGs */
input[type="date"]::before,
input[type="text"]::before,
input[type="number"]::before {
  content: '\f073'; /* Example icon code */
  font-family: 'FontAwesome'; /* Example icon font */
  margin-right: 10px;
}

/* Add background colors for input fields and buttons */
input[type="date"]:focus,
input[type="text"]:focus,
input[type="number"]:focus {
  border-color: #007bff;
  outline: none;
}

button:active {
  background-color: #004085;
}
